
*{
  margin: 0;
  padding: 0;
}

.nav{
  background-color: rgb(243, 243, 243);
  display: flex;
  align-items: center;
}

.logo1{
  padding: 1px 20px;
  width: 50px;
  height: 50px;
}

.ulitems{
  margin-left: 65vw;
  display: flex;
}

.li,.li2{
  padding: 0 15px;
  list-style-type: none;
  }

.li:hover{
  cursor: pointer;
}

.b1, .b2, .b3{
  border-right: 1px solid black;
}

.bar2{
  background-color: rgb(255, 255, 255);

}

.uldiv{
  width: 70vw;
}

.navbar2{
  margin-left: 28vw;
  display: flex;
  justify-content: center;
  height: 55px;
}

.li2{
  display: inline;
  padding: 15px 15px;
  font-size: 15px;
}

.bd:hover{
  cursor: pointer;
  border-bottom: 2px solid black;
}

.searchbox{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(243, 243, 243);
  border-radius: 30px;
  margin: 5px 0 ;
  width: 180px;
  padding: 5px;
}

.searchinput{
  width: 130px;
  font-size: 15px;
  border: none;
  outline: none;
  border-radius: 10px;
  height: 25px;
  padding: 2px;
  background-color: rgba(243, 243, 243, 0);
}

.searchimage{
  width: 30px;
  height: 30px;
  padding: 2px;
}

.searchbox:hover{
  background-color: rgb(207, 207, 207)
}

.iconbox{
  display: flex;
  margin: 5px 0;
  height: 50px;
  justify-content: center;
}

.navimg{
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
}

.btn2{
  padding: 10px;
  height: 50px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  background-color: rgba(128, 128, 128, 0);
}

.shopcartimg, .likeimg{
  margin: 0 20px;
  display: inline;
}

.shopcartimg:hover
, .likeimg:hover{
  display: inline;
}

.btn2:hover{
  background-color:  rgb(207, 207, 207);
}

.container{
  width: 100vw;
  overflow: hidden;
}

.slider-wrapper{
  display: flex;
  width: 200vw;
  height: 60px;
}

.slider{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sl-1{
  transform: translate(-50%);
  transition-duration: 2s;
}
.sl-2{
  transition-duration: 2s;
  transform: translate(0);
}

.s-1, .s-2{
  background-color: rgb(243, 243, 243);
  width: 100vw;
} 